'use client';

import { useEffect, useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Badge } from '@/components/ui/badge';
import { Worker, Attendance } from '@/lib/types';
import { getWorkers, getAttendance, formatDate } from '@/lib/storage';

interface ReportData {
  worker: Worker;
  totalDays: number;
  presentDays: number;
  absentDays: number;
  totalHours: number;
  totalEarnings: number;
  attendanceRate: number;
}

export default function ReportsPage() {
  const [workers, setWorkers] = useState<Worker[]>([]);
  const [attendance, setAttendance] = useState<Attendance[]>([]);
  const [reportData, setReportData] = useState<ReportData[]>([]);
  const [loading, setLoading] = useState(true);
  const [dateRange, setDateRange] = useState({
    startDate: '',
    endDate: formatDate(new Date())
  });

  useEffect(() => {
    loadData();
  }, []);

  useEffect(() => {
    if (workers.length > 0 && attendance.length > 0) {
      generateReport();
    }
  }, [workers, attendance, dateRange]);

  const loadData = () => {
    try {
      const workersData = getWorkers();
      const attendanceData = getAttendance();
      
      setWorkers(workersData);
      setAttendance(attendanceData);

      // Set default start date to 30 days ago
      const thirtyDaysAgo = new Date();
      thirtyDaysAgo.setDate(thirtyDaysAgo.getDate() - 30);
      setDateRange(prev => ({
        ...prev,
        startDate: formatDate(thirtyDaysAgo)
      }));
    } catch (error) {
      console.error('Error loading data:', error);
    } finally {
      setLoading(false);
    }
  };

  const generateReport = () => {
    try {
      const filteredAttendance = attendance.filter(record => {
        const recordDate = record.date;
        const isInRange = (!dateRange.startDate || recordDate >= dateRange.startDate) &&
                         (!dateRange.endDate || recordDate <= dateRange.endDate);
        return isInRange;
      });

      const report = workers.map(worker => {
        const workerAttendance = filteredAttendance.filter(a => a.workerId === worker.id);
        const presentDays = workerAttendance.filter(a => a.status === 'present' || a.status === 'half-day').length;
        const absentDays = workerAttendance.filter(a => a.status === 'absent').length;
        const totalHours = workerAttendance.reduce((sum, a) => sum + a.totalHours, 0);
        const totalEarnings = totalHours * worker.wageRate;
        const totalDays = workerAttendance.length;
        const attendanceRate = totalDays > 0 ? (presentDays / totalDays) * 100 : 0;

        return {
          worker,
          totalDays,
          presentDays,
          absentDays,
          totalHours,
          totalEarnings,
          attendanceRate
        };
      });

      // Sort by total hours descending
      report.sort((a, b) => b.totalHours - a.totalHours);
      setReportData(report);
    } catch (error) {
      console.error('Error generating report:', error);
    }
  };

  const exportToCSV = () => {
    try {
      const headers = [
        'Worker Name',
        'Role',
        'Total Days',
        'Present Days',
        'Absent Days',
        'Total Hours',
        'Attendance Rate (%)',
        'Hourly Rate ($)',
        'Total Earnings ($)'
      ];

      const csvData = reportData.map(data => [
        data.worker.name,
        data.worker.role,
        data.totalDays,
        data.presentDays,
        data.absentDays,
        data.totalHours.toFixed(1),
        data.attendanceRate.toFixed(1),
        data.worker.wageRate.toFixed(2),
        data.totalEarnings.toFixed(2)
      ]);

      const csvContent = [headers, ...csvData]
        .map(row => row.map(field => `"${field}"`).join(','))
        .join('\n');

      const blob = new Blob([csvContent], { type: 'text/csv' });
      const url = window.URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = `attendance-report-${dateRange.startDate}-to-${dateRange.endDate}.csv`;
      link.click();
      window.URL.revokeObjectURL(url);
    } catch (error) {
      console.error('Error exporting CSV:', error);
      alert('Failed to export CSV. Please try again.');
    }
  };

  const getTotalStats = () => {
    return reportData.reduce((totals, data) => ({
      totalWorkers: reportData.length,
      totalHours: totals.totalHours + data.totalHours,
      totalEarnings: totals.totalEarnings + data.totalEarnings,
      averageAttendance: reportData.length > 0 
        ? reportData.reduce((sum, d) => sum + d.attendanceRate, 0) / reportData.length 
        : 0
    }), { totalWorkers: 0, totalHours: 0, totalEarnings: 0, averageAttendance: 0 });
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-64">
        <div className="text-lg text-gray-600">Loading reports...</div>
      </div>
    );
  }

  const totalStats = getTotalStats();

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex justify-between items-center">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Attendance Reports</h1>
          <p className="text-gray-600 mt-1">Analyze worker attendance and performance</p>
        </div>
        <div className="flex gap-3">
          <Button variant="outline" onClick={exportToCSV} disabled={reportData.length === 0}>
            Export CSV
          </Button>
          <Button variant="outline" asChild>
            <a href="/attendance">Back to Attendance</a>
          </Button>
        </div>
      </div>

      {/* Date Range Filter */}
      <Card>
        <CardHeader>
          <CardTitle>Report Filters</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="startDate">Start Date</Label>
              <Input
                id="startDate"
                type="date"
                value={dateRange.startDate}
                onChange={(e) => setDateRange(prev => ({ ...prev, startDate: e.target.value }))}
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="endDate">End Date</Label>
              <Input
                id="endDate"
                type="date"
                value={dateRange.endDate}
                onChange={(e) => setDateRange(prev => ({ ...prev, endDate: e.target.value }))}
              />
            </div>
          </div>
          <div className="mt-4 text-sm text-gray-600">
            Report period: {dateRange.startDate ? new Date(dateRange.startDate).toLocaleDateString() : 'All time'} 
            {' to '} 
            {dateRange.endDate ? new Date(dateRange.endDate).toLocaleDateString() : 'Present'}
          </div>
        </CardContent>
      </Card>

      {/* Summary Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        <Card>
          <CardContent className="pt-6">
            <div className="text-2xl font-bold">{totalStats.totalWorkers}</div>
            <p className="text-xs text-gray-600">Total Workers</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="text-2xl font-bold text-blue-600">{totalStats.totalHours.toFixed(1)}</div>
            <p className="text-xs text-gray-600">Total Hours</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="text-2xl font-bold text-green-600">${totalStats.totalEarnings.toFixed(2)}</div>
            <p className="text-xs text-gray-600">Total Earnings</p>
          </CardContent>
        </Card>
        <Card>
          <CardContent className="pt-6">
            <div className="text-2xl font-bold text-purple-600">{totalStats.averageAttendance.toFixed(1)}%</div>
            <p className="text-xs text-gray-600">Avg Attendance</p>
          </CardContent>
        </Card>
      </div>

      {/* Detailed Report Table */}
      <Card>
        <CardHeader>
          <CardTitle>Worker Performance Report</CardTitle>
        </CardHeader>
        <CardContent>
          {reportData.length > 0 ? (
            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="border-b">
                    <th className="text-left py-3">Worker</th>
                    <th className="text-left py-3">Role</th>
                    <th className="text-center py-3">Days</th>
                    <th className="text-center py-3">Present</th>
                    <th className="text-center py-3">Absent</th>
                    <th className="text-center py-3">Hours</th>
                    <th className="text-center py-3">Rate</th>
                    <th className="text-right py-3">Earnings</th>
                    <th className="text-center py-3">Attendance</th>
                  </tr>
                </thead>
                <tbody>
                  {reportData.map((data) => (
                    <tr key={data.worker.id} className="border-b hover:bg-gray-50">
                      <td className="py-3">
                        <div>
                          <p className="font-medium">{data.worker.name}</p>
                          <p className="text-sm text-gray-600">{data.worker.phone}</p>
                        </div>
                      </td>
                      <td className="py-3">
                        <Badge className={
                          data.worker.role === 'manager' ? 'bg-purple-100 text-purple-800' :
                          data.worker.role === 'supervisor' ? 'bg-blue-100 text-blue-800' :
                          'bg-green-100 text-green-800'
                        }>
                          {data.worker.role}
                        </Badge>
                      </td>
                      <td className="text-center py-3">{data.totalDays}</td>
                      <td className="text-center py-3 text-green-600 font-medium">{data.presentDays}</td>
                      <td className="text-center py-3 text-red-600 font-medium">{data.absentDays}</td>
                      <td className="text-center py-3 font-medium">{data.totalHours.toFixed(1)}</td>
                      <td className="text-center py-3">${data.worker.wageRate}</td>
                      <td className="text-right py-3 font-medium text-green-600">
                        ${data.totalEarnings.toFixed(2)}
                      </td>
                      <td className="text-center py-3">
                        <Badge className={
                          data.attendanceRate >= 90 ? 'bg-green-100 text-green-800' :
                          data.attendanceRate >= 75 ? 'bg-yellow-100 text-yellow-800' :
                          'bg-red-100 text-red-800'
                        }>
                          {data.attendanceRate.toFixed(1)}%
                        </Badge>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          ) : (
            <div className="text-center py-8">
              <p className="text-gray-600">No attendance data found for the selected period</p>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Performance Insights */}
      {reportData.length > 0 && (
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <Card>
            <CardHeader>
              <CardTitle>Top Performers</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                {reportData
                  .filter(d => d.totalDays > 0)
                  .sort((a, b) => b.attendanceRate - a.attendanceRate)
                  .slice(0, 5)
                  .map((data, index) => (
                    <div key={data.worker.id} className="flex justify-between items-center">
                      <div>
                        <p className="font-medium">{data.worker.name}</p>
                        <p className="text-sm text-gray-600">{data.totalHours.toFixed(1)} hours</p>
                      </div>
                      <Badge className="bg-green-100 text-green-800">
                        {data.attendanceRate.toFixed(1)}%
                      </Badge>
                    </div>
                  ))}
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Highest Earners</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                {reportData
                  .sort((a, b) => b.totalEarnings - a.totalEarnings)
                  .slice(0, 5)
                  .map((data) => (
                    <div key={data.worker.id} className="flex justify-between items-center">
                      <div>
                        <p className="font-medium">{data.worker.name}</p>
                        <p className="text-sm text-gray-600">{data.totalHours.toFixed(1)} hours</p>
                      </div>
                      <div className="text-right">
                        <p className="font-medium text-green-600">${data.totalEarnings.toFixed(2)}</p>
                      </div>
                    </div>
                  ))}
              </div>
            </CardContent>
          </Card>
        </div>
      )}
    </div>
  );
}
